import Vue from 'vue'
import NP from 'number-precision'

export default {
  namespaced: true,
  state() {
    return {
      cart: []
    }
  },
  getters: {
    // 
    getNumInCart: ({cart}) => (id) => {
      let num = 0
      cart.forEach(item => {
        if (item.data.id === id) num = item.num
      })
      return num
    },
    // 
    totalNum({cart}) {
      let num = 0
      cart.forEach(item => {
        num += item.num
      })
      return num
    },
    // 
    totalPrice({cart}) {
      let totalPrice = 0
      cart.forEach(item => {
        totalPrice = NP.plus(totalPrice, item.price)
      })
      return totalPrice
    },
    // 
    originalTotal({cart}) {
      let totalPrice = 0
      cart.forEach(item => {
        let price = NP.times(item.data.originalPrice, item.num)
        totalPrice = NP.plus(totalPrice, price)
      })
      return totalPrice
    },
  },
  mutations: {
    // 
    clearCart(state) {
      state.cart = []
    },
    // 
    addToCart({cart}, data) {
      let isExisted = false
      let price = data.presentPrice || data.originalPrice
      cart.forEach(item => {
        if (item.data.id === data.id) {
          isExisted = true
          // 
          if (data.maxNum && data.maxNum <= item.num) {
            Vue.prototype.$toast.warning('')
            return
          }
          // 
          if (data.limitNum && data.limitNum <= item.num) {
            Vue.prototype.$toast(`${data.limitNum}`)
            price = data.originalPrice || 0
          }
          item.num++
          item.price = NP.plus(item.price, price)
        }
      })
      !isExisted && cart.push({data, num: 1, price})
    },
    // 
    removeFromCart({cart}, data) {
      let price = data.presentPrice || data.originalPrice
      cart.forEach((item, index) => {
        if (item.data.id === data.id) {
          if (item.num === 1) {
            cart.splice(index, 1)
            return
          }
          if (data.limitNum && data.limitNum <= item.num) {
            price = data.originalPrice
          }
          item.num--
          item.price = NP.minus(item.price, price)
        }
      })
    },
  }
}



