<template>
  <div class="change-nickname">
    <v-header title="" class="u-fixed-header"/>
    <div class="u-main-content fixed ">
      <div class="input__wrapper">
        <span class="input__title"></span>
        <v-input type="text" placeholder="" v-model="nickname"/>
      </div>
      <v-button type="primary" @click="changeNickname"></v-button>
    </div>
  </div>
</template>

<script>
  import { mapActions, mapMutations } from 'vuex'
  export default {
    name: "change-nickname",
    props: [],
    components: {},
    data() {
      return {
        nickname: null,
        changeLoading:false
      }
    },
    computed: {},
    mounted() {
    },
    methods: {
      ...mapActions('user', ['ajaxChangeUserInfo']),
      ...mapMutations('user', ['setUser']),
      async changeNickname() {
        if (this.changeLoading) return
        let { nickname } = this
        if (!nickname) {
          this.$toast.error('')
          return
        }
        this.changeLoading = true
        this.$toast.loading('...')
        let res = await this.ajaxChangeUserInfo({ nichName: nickname })
        if (this.$checkData(res)) {
          this.setUser(res.data.user);
          let auth = { headers: { Authorization: res.data.token } }
          localStorage.setItem('auth', JSON.stringify(auth))
          this.$toast.success('')
          setTimeout(() => {
            this.$router.go(-1);
          }, 1500)
        }
        this.changeLoading = false
      },
    }
  }
</script>

<style scoped lang="scss">
  .change-nickname {
    .input__wrapper {
      padding: 0 .75rem;
      background: #fff;
      margin-bottom: 1.5rem;
      span.input__title{
        font-size: 15px;
        color: #CCCCCC;
      }
      .v-input{
        display: inline-block;
        border: 0;
      }
    }
  }
</style>



